/*-------- food_fruit --------*/
.food_fruit {
  width: 1240px;
  margin: 0 auto;
}

.food_fruit .title {
  width: 1240px; height: 100px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  line-height: 100px;
  margin-top: 50px;
}

/*-------- category_box --------*/
.food_fruit .category_box {
  width: 1240px; height: 170px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  margin-bottom: 50px;
  overflow: hidden;
}

.category_box .category {
  font-size: 14px;
  font-weight: bold;
  padding: 15px 0 15px 20px;
  box-sizing: border-box;
}
.category_box .category_li ul {
  width: 285px; height: 100px;
  font-size: 14px;
  float: left;
  margin-left: 20px;
}
.category_box .category_li li {margin-bottom: 5px;}
.category_box .category_li li:last-child {margin-bottom: 0;}
.category_box .category_li li a:hover {
  font-weight: 600;
}


/*-------- product_box --------*/
.product_box {
  display: flex;
  flex-wrap: wrap;
  width: 1240px;
}
.product_box .product {
  margin-right: 20px;
}
.product_box .product:nth-child(4n) {
  margin-right: 0;
  margin-bottom: 20px;
}

.product_box .img {
  width: 295px; height: 350px;
  margin-bottom: 10px;
}
.product_box .text {
  width: 295px; height: 100px;
  font-size: 0;
}
.product_box .text li {font-size: 12px;}
.product_box .text .emart_mall {
  display: inline-block;
  width: 55px; height: 20px;
  background-color: #FFD140;
  font-weight: bold;
  text-align: center;
  line-height: 20px;
  margin-right: 5px;
  margin-bottom: 5px;
}
.product_box .text .delivery {
  display: inline-block;
  width: 55px; height: 20px;
  background-color: #8496AD;
  color: white;
  text-align: center;
  line-height: 20px;
  margin-right: 5px;
  margin-bottom: 5px;
}
.product_box .text .fresh {
  display: inline-block;
  color: red;
}
.product_box .text .emart_fresh {
  font-weight: bold;
}
.product_box .text .price {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  margin-right: 2px;
}
.product_box .text .won {
  display: inline-block;
}
.product_box .text .sale {
  display: inline-block;
  color: #666;
  text-decoration: line-through;
  margin-left: 10px;
}
.product_box .text .sale_won {
  display: inline-block;
  color: #666;
  margin-left: 2px;
}


/*-------- page_nation --------*/
#page_left, #page01, #page02, #page03, #page04, #page05, #page06, #page07, #page08, #page09, #page10, #page_next, #page_right {display: none;}

#page01:checked ~ .label label:nth-child(2),
#page02:checked ~ .label label:nth-child(3),
#page03:checked ~ .label label:nth-child(4),
#page04:checked ~ .label label:nth-child(5),
#page05:checked ~ .label label:nth-child(6),
#page06:checked ~ .label label:nth-child(7),
#page07:checked ~ .label label:nth-child(8),
#page08:checked ~ .label label:nth-child(9),
#page09:checked ~ .label label:nth-child(10),
#page10:checked ~ .label label:nth-child(11) {
  background-color: black;
  color: white;
}

.page_nation {
  width: 1240px; height: 30px;
  margin: 100px auto;
}
.page_nation .label {text-align: center;}
.page_nation .label label {
  display: inline-block;
  width: 30px; height: 30px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  font-size: 12px;
  text-align: center;
  line-height: 28px;
}
.page_nation .label label:nth-child(1),
.page_nation .label label:nth-child(12),
.page_nation .label label:nth-child(13) {background-color: #eee;}
.page_nation .label label:hover {cursor: pointer;}